export interface ApiResponse<T = unknown> {
  success: boolean
  message: string
  data?: T
  unauthorized?: boolean
}

export async function callApi<T>(
  endpoint: string,
  body?: Record<string, unknown>,
  method: "GET" | "POST" = "POST"
): Promise<ApiResponse<T>> {
  try {
    // ‚úÖ ‡∏ñ‡πâ‡∏≤‡πÄ‡∏õ‡πá‡∏ô GET ‚Üí ‡πÅ‡∏õ‡∏•‡∏á body ‡πÄ‡∏õ‡πá‡∏ô query string
    if (method === "GET" && body) {
      // ‡∏ñ‡πâ‡∏≤‡∏°‡∏µ key data ‚Üí flatten data ‡∏Ç‡∏∂‡πâ‡∏ô‡∏°‡∏≤
      const querySource =
        body.data && typeof body.data === "object"
          ? { ...body, ...body.data }
          : body

      const params = new URLSearchParams(
        Object.entries(querySource).reduce((acc, [k, v]) => {
          // ‡πÑ‡∏°‡πà‡∏ï‡πâ‡∏≠‡∏á‡πÉ‡∏™‡πà key data ‡∏•‡∏á‡πÑ‡∏õ
          if (k === "data") return acc
          acc[k] = String(v ?? "")
          return acc
        }, {} as Record<string, string>)
      ).toString()

      endpoint += `?${params}`
    }

    
    const res = await fetch(endpoint, {
      method,
      headers: { "Content-Type": "application/json" },
      body: method === "POST" ? JSON.stringify(body) : undefined,
      credentials: "include",
      cache: "no-store",
    })
    
    const data = await res.json().catch(() => ({}))
    const message = data.message || `‡πÄ‡∏Å‡∏¥‡∏î‡∏Ç‡πâ‡∏≠‡∏ú‡∏¥‡∏î‡∏û‡∏•‡∏≤‡∏î‡πÉ‡∏ô‡∏£‡∏∞‡∏ö‡∏ö (${res.status})`

    if (res.status === 401) {
      return { success: false, message, unauthorized: true }
    }

    if (!res.ok) {
      return { success: false, message }
    }

    return {
      success: true,
      message: data.message || "‡∏î‡∏≥‡πÄ‡∏ô‡∏¥‡∏ô‡∏Å‡∏≤‡∏£‡∏™‡∏≥‡πÄ‡∏£‡πá‡∏à",
      data,
    }
  } catch (err) {
    console.error("‚ùå callApi error:", err)
    return {
      success: false,
      message: "‡πÑ‡∏°‡πà‡∏™‡∏≤‡∏°‡∏≤‡∏£‡∏ñ‡πÄ‡∏ä‡∏∑‡πà‡∏≠‡∏°‡∏ï‡πà‡∏≠‡∏Å‡∏±‡∏ö‡πÄ‡∏ã‡∏¥‡∏£‡πå‡∏ü‡πÄ‡∏ß‡∏≠‡∏£‡πå‡πÑ‡∏î‡πâ",
    }
  }
}

